import { Injectable } from '@angular/core';
import { SwUpdate } from '@angular/service-worker';
import { AlertController, ToastController } from '@ionic/angular';
import { filter, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PwaUpdateService {

  constructor(
    private swUpdate: SwUpdate,
    private alertController: AlertController,
    private toastController: ToastController
  ) {
    this.checkForUpdates();
  }

  private checkForUpdates() {
    if (this.swUpdate.isEnabled) {
      // Verificar actualizaciones al iniciar la aplicaci√≥n
      this.swUpdate.checkForUpdate();
      
      // Verificar actualizaciones cada 30 minutos (en lugar de 6 horas)
      setInterval(() => {
        this.swUpdate.checkForUpdate();
      }, 30 * 60 * 1000);

      // Escuchar actualizaciones disponibles
      this.swUpdate.versionUpdates
        .pipe(
          filter(event => event.type === 'VERSION_READY')
        )
        .subscribe(() => {
          this.promptUser();
        });

      // Escuchar errores de actualizaci√≥n
      this.swUpdate.versionUpdates
        .pipe(
          filter(event => event.type === 'VERSION_INSTALLATION_FAILED')
        )
        .subscribe(() => {
          this.showError();
        });

      // Escuchar cuando hay una nueva versi√≥n disponible
      this.swUpdate.versionUpdates
        .pipe(
          filter(event => event.type === 'VERSION_DETECTED')
        )
        .subscribe(() => {
          console.log('Nueva versi√≥n detectada');
        });
    }
  }

  private async promptUser() {
    const alert = await this.alertController.create({
      header: 'Nueva versi√≥n disponible',
      message: 'Hay una nueva versi√≥n de la aplicaci√≥n disponible. ¬øDeseas actualizar ahora?',
      buttons: [
        {
          text: 'M√°s tarde',
          role: 'cancel'
        },
        {
          text: 'Actualizar',
          handler: () => {
            this.activateUpdate();
          }
        }
      ]
    });

    await alert.present();
  }

  private async showError() {
    const toast = await this.toastController.create({
      message: 'Error al actualizar la aplicaci√≥n',
      duration: 3000,
      position: 'bottom',
      color: 'danger'
    });

    await toast.present();
  }

  private activateUpdate() {
    this.swUpdate.activateUpdate().then(() => {
      window.location.reload();
    });
  }

  // M√©todo p√∫blico para verificar actualizaciones manualmente
  public checkForUpdate() {
    if (this.swUpdate.isEnabled) {
      this.swUpdate.checkForUpdate().then(() => {
        console.log('Verificando actualizaciones...');
      });
    }
  }

  // M√©todo para forzar la actualizaci√≥n sin preguntar al usuario
  public forceUpdate() {
    if (this.swUpdate.isEnabled) {
      this.swUpdate.activateUpdate().then(() => {
        window.location.reload();
      });
    }
  }

  // M√©todo para limpiar el cache del Service Worker
  public clearCache() {
    if ('caches' in window) {
      caches.keys().then(names => {
        names.forEach(name => {
          caches.delete(name);
        });
        console.log('Cache limpiado');
        window.location.reload();
      });
    }
  }
} 