import { Injectable } from '@angular/core';
import { Platform } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class PwaIosService {

  constructor(private platform: Platform) {
    this.initializeMobilePWA();
  }

  private initializeMobilePWA() {
    // Detectar si estamos en iOS o Android
    if (this.platform.is('ios') || this.platform.is('android')) {
      this.setupMobilePWA();
    }
  }

  private setupMobilePWA() {
    // Prevenir el zoom en dispositivos m√≥viles
    document.addEventListener('gesturestart', (e) => {
      e.preventDefault();
    });

    // Prevenir el zoom con doble tap
    document.addEventListener('touchend', (e) => {
      const now = Date.now();
      const lastTouch = (window as any).lastTouch || 0;
      const timeDiff = now - lastTouch;
      
      if (timeDiff < 500 && timeDiff > 0) {
        e.preventDefault();
      }
      
      (window as any).lastTouch = now;
    });

    // Configurar el viewport para dispositivos m√≥viles
    this.setupViewport();
    
    // Agregar estilos espec√≠ficos para m√≥viles
    this.addMobileStyles();
  }

  private setupViewport() {
    const viewport = document.querySelector('meta[name="viewport"]');
    if (viewport) {
      const content = [
        'viewport-fit=cover',
        'width=device-width',
        'initial-scale=1.0',
        'minimum-scale=1.0',
        'maximum-scale=1.0',
        'user-scalable=no'
      ].join(', ');
      
      viewport.setAttribute('content', content);
    }
  }

  private addMobileStyles() {
    const style = document.createElement('style');
    style.textContent = `
      /* Estilos espec√≠ficos para dispositivos m√≥viles */
      
      /* Configuraci√≥n base */
      html {
        height: 100%;
        overflow: hidden;
      }
      
      body {
        height: 100%;
        margin: 0;
        padding: 0;
        overflow: hidden;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -webkit-tap-highlight-color: transparent;
      }
      
      /* Configuraci√≥n para PWA standalone */
      @media all and (display-mode: standalone) {
        body {
          /* Aplicar safe areas solo en modo standalone */
          padding-top: env(safe-area-inset-top, 0px);
          padding-bottom: env(safe-area-inset-bottom, 0px);
          padding-left: env(safe-area-inset-left, 0px);
          padding-right: env(safe-area-inset-right, 0px);
        }
        
        /* Configurar ion-app */
        ion-app {
          height: 100vh;
          height: calc(100vh - env(safe-area-inset-top, 0px) - env(safe-area-inset-bottom, 0px));
          margin: 0;
          padding: 0;
        }
        
        /* Configurar ion-content */
        ion-content {
          --offset-top: 0px;
          --offset-bottom: 0px;
          --padding-top: 0px;
          --padding-bottom: 0px;
        }
        
        /* Configurar ion-header */
        ion-header {
          padding-top: 0px;
        }
        
        /* Configurar ion-footer */
        ion-footer {
          padding-bottom: 0px;
        }
        
        /* Configurar ion-toolbar */
        ion-toolbar {
          --padding-top: 0px;
          --padding-bottom: 0px;
        }
      }
      
      /* Configuraci√≥n espec√≠fica para iOS */
      @supports (-webkit-touch-callout: none) {
        body {
          /* Prevenir el bounce scroll en iOS */
          position: fixed;
          width: 100%;
          height: 100%;
        }
        
        ion-content {
          overflow-y: auto;
          -webkit-overflow-scrolling: touch;
        }
      }
      
      /* Configuraci√≥n para Android */
      @supports not (-webkit-touch-callout: none) {
        body {
          /* Configuraci√≥n espec√≠fica para Android */
          position: relative;
        }
      }
      
      /* Asegurar que el contenido principal ocupe todo el espacio */
      .ion-page {
        height: 100%;
        display: flex;
        flex-direction: column;
      }
      
      /* Configurar el router outlet para que ocupe el espacio disponible */
      ion-router-outlet {
        flex: 1;
        height: 100%;
      }
    `;
    document.head.appendChild(style);
  }

  // M√©todo para mostrar instrucciones de instalaci√≥n espec√≠ficas de iOS
  public showIOSInstallInstructions() {
    const isStandalone = (window.navigator as any).standalone === true;
    
    if (!isStandalone) {
      // Mostrar instrucciones espec√≠ficas para iOS
      const instructions = `
        Para instalar esta aplicaci√≥n en tu iPhone:
        
        1. Toca el bot√≥n compartir (‚ñ°‚Üë) en Safari
        2. Despl√°zate hacia abajo y toca "Agregar a pantalla de inicio"
        3. Toca "Agregar"
        
        La aplicaci√≥n aparecer√° en tu pantalla de inicio como una app nativa.
      `;
      
      alert(instructions);
    }
  }

  // M√©todo para verificar si la app est√° en modo standalone
  public isStandalone(): boolean {
    return (window.navigator as any).standalone === true || 
           window.matchMedia('(display-mode: standalone)').matches;
  }

  // M√©todo para obtener informaci√≥n de las safe areas
  public getSafeAreaInfo(): any {
    return {
      top: this.getComputedValue('env(safe-area-inset-top)'),
      bottom: this.getComputedValue('env(safe-area-inset-bottom)'),
      left: this.getComputedValue('env(safe-area-inset-left)'),
      right: this.getComputedValue('env(safe-area-inset-right)')
    };
  }

  private getComputedValue(property: string): string {
    const testElement = document.createElement('div');
    testElement.style.position = 'absolute';
    testElement.style.visibility = 'hidden';
    testElement.style[property as any] = property;
    document.body.appendChild(testElement);
    
    const computedValue = getComputedStyle(testElement)[property as any];
    document.body.removeChild(testElement);
    
    return computedValue || '0px';
  }
} 